{
    --playerOne: rgb(207, 60, 60);
    --playerTwo: rgb(70, 70, 211);
}

body
{
    display: grid;
    align-content: center;
    height:100vh;
    width:100vw;
    margin:0px;
}

h3
{
    text-align: center;
}

button:hover
{
    cursor: pointer;
}

.first-cover
{
    background-color: var(--playerOne);
}

.second-cover
{
    background-color: var(--playerTwo);
}

.screen-cover
{
    width: 100vw;
    height:100vh;
    position: absolute;
    justify-content: center;
    align-content: center;
    display: grid;
    color: white;
}

.turn-cover
{
    width: fit-content;
    height: fit-content;
    display: grid;
}

.gamemode-options
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    gap:16px
}

.gamemode-option
{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:32px
}

.gamemode-option button
{
    width: 30%;
    background-color: white;
    font-size: x-large;
    border: none;
    border-radius: 16px;
}

.gamemode-select img
{
    max-width: 50%;
    aspect-ratio: 1 / 1;
}

.game-end-screen
{
    width:fit-content;
    height:fit-content;
    display: flex;
    flex-direction: column;
    gap:16px;
    align-items: center;
}

#button-restart-game
{
    width: fit-content;
    justify-self: center;
    font-size: x-large;
    background-color: white;
    border: none;
    padding: 16px;
    border-radius: 16px;
}

.battleship-container
{
    display: grid;
    grid-template-columns: 25% 1fr 25%;
    margin:32px;
    padding:16px;
    gap:32px;
    place-items: center;
}

.sidebar
{
    border: 4px solid black;
    border-radius: 32px;
    width: 100%;
    padding: 8px;
    background-color: rgba(255, 255, 255, 0.5);
    height:100%;
    max-height: 100vh;
}

.container
{
    display: grid;
}

.current-ship-indicator
{
    display: grid;
    justify-content: center;
    align-content: center;
}

.ship-controls-container
{
    display: grid;
    grid-template-rows: 2fr 6fr;
    width: 100%;
    height: 100%;
}

.button-container
{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.sidebar-button
{
    align-self: center;
    width: fit-content;
    justify-self: center;
    background-color: transparent;
    border: solid 1px black;
    border-radius: 4px;
    text-wrap: auto;
    font-size: xx-large;
}

#right-sidebar
{
    display: flex;
    flex-direction: column;
}
.right-sidebar
{
    display: grid;
    grid-template-rows: 10% 90%;
}


.right-sidebar .ship-list
{
    display: flex;
    align-content: center;
    flex-direction: column;
    gap:32px;
    height:100%;
}

.ship-list button
{
    height: 10%;
    background-color: transparent;
    border: none;
}

.ship-display
{
    display: flex;
    justify-content: flex-end;
}
.carrier
{
    grid-template-columns: repeat(5, 1fr);
}
.destroyer
{
    grid-template-columns: repeat(4, 1fr);
}
.submarine
{
    grid-template-columns: repeat(3, 1fr);
}
.tug{
    grid-template-columns: repeat(2, 1fr);
    
}

.ship-list
{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
}

.ship-tile
{
    aspect-ratio: 1 / 1;
    border: 2px black solid;
    width: 10%;
    border-radius: 8px;
}

#content
{
    padding: 0px 5%;
    width:100%;
}

#board-visual
{
    border-radius: 8px;
    z-index: 99;
    pointer-events: none;
}

.ship-place-error
{
    background-color: rgb(255, 94, 0);
}

.ship-place-good
{
    background-color: orange;
}

.board-container
{
    display: grid;
    grid-template-rows: 1fr 7fr 1fr;
    align-content: center;
    aspect-ratio: 1/1;
    width:100%;
}

.board-container h3
{
    text-align: center;
}

.board
{
    background-color: rgb(70, 70, 107);
    aspect-ratio: 1 / 1;
    justify-self: center;
    border-radius: 16px;
    padding:4px;
    display:grid;  
    grid-template-columns: repeat(9 , 1fr);
    grid-template-rows: repeat(9,1fr);
    border-radius: 16px;

}

.game-left-container
{
display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
}

#confirm-button
{
    margin-top: auto;
    margin-bottom: 25%;
    width: 60%;
    align-self: center;
}

.update-text-container
{
    text-align: center;
}

.tile
{
    border-radius: 8px;
    border: 2px rgb(44, 163, 163) solid;
    background-color: transparent;
}

.tile:hover
{
    cursor: pointer;
}


.miss
{
    background-color: white;
}

.hit
{
    background-color: red;
}

.sunk
{
    background-color: purple;
}

.ship-exists
{
    background-color: orange;
}`, "",{"version":3,"sources":["webpack://./src/css/styles.css"],"names":[],"mappings":"AAAA;;IAEI,6BAA6B;IAC7B,6BAA6B;AACjC;;AAEA;;IAEI,aAAa;IACb,qBAAqB;IACrB,YAAY;IACZ,WAAW;IACX,UAAU;AACd;;AAEA;;IAEI,kBAAkB;AACtB;;AAEA;;IAEI,eAAe;AACnB;;AAEA;;IAEI,kCAAkC;AACtC;;AAEA;;IAEI,kCAAkC;AACtC;;AAEA;;IAEI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,uBAAuB;IACvB,qBAAqB;IACrB,aAAa;IACb,YAAY;AAChB;;AAEA;;IAEI,kBAAkB;IAClB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;;IAEI,aAAa;IACb,8BAA8B;IAC9B,uBAAuB;IACvB;AACJ;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB;AACJ;;AAEA;;IAEI,UAAU;IACV,uBAAuB;IACvB,kBAAkB;IAClB,YAAY;IACZ,mBAAmB;AACvB;;AAEA;;IAEI,cAAc;IACd,mBAAmB;AACvB;;AAEA;;IAEI,iBAAiB;IACjB,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,mBAAmB;AACvB;;AAEA;;IAEI,kBAAkB;IAClB,oBAAoB;IACpB,kBAAkB;IAClB,uBAAuB;IACvB,YAAY;IACZ,aAAa;IACb,mBAAmB;AACvB;;AAEA;;IAEI,aAAa;IACb,kCAAkC;IAClC,WAAW;IACX,YAAY;IACZ,QAAQ;IACR,mBAAmB;AACvB;;AAEA;;IAEI,uBAAuB;IACvB,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,0CAA0C;IAC1C,WAAW;IACX,iBAAiB;AACrB;;AAEA;;IAEI,aAAa;AACjB;;AAEA;;IAEI,aAAa;IACb,uBAAuB;IACvB,qBAAqB;AACzB;;AAEA;;IAEI,aAAa;IACb,2BAA2B;IAC3B,WAAW;IACX,YAAY;AAChB;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,6BAA6B;AACjC;;AAEA;;IAEI,kBAAkB;IAClB,kBAAkB;IAClB,oBAAoB;IACpB,6BAA6B;IAC7B,uBAAuB;IACvB,kBAAkB;IAClB,eAAe;IACf,mBAAmB;AACvB;;AAEA;;IAEI,aAAa;IACb,sBAAsB;AAC1B;AACA;;IAEI,aAAa;IACb,2BAA2B;AAC/B;;;AAGA;;IAEI,aAAa;IACb,qBAAqB;IACrB,sBAAsB;IACtB,QAAQ;IACR,WAAW;AACf;;AAEA;;IAEI,WAAW;IACX,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;;IAEI,aAAa;IACb,yBAAyB;AAC7B;AACA;;IAEI,qCAAqC;AACzC;AACA;;IAEI,qCAAqC;AACzC;AACA;;IAEI,qCAAqC;AACzC;AACA;IACI,qCAAqC;;AAEzC;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;;IAEI,mBAAmB;IACnB,uBAAuB;IACvB,UAAU;IACV,kBAAkB;AACtB;;AAEA;;IAEI,eAAe;IACf,UAAU;AACd;;AAEA;;IAEI,kBAAkB;IAClB,WAAW;IACX,oBAAoB;AACxB;;AAEA;;IAEI,iCAAiC;AACrC;;AAEA;;IAEI,wBAAwB;AAC5B;;AAEA;;IAEI,aAAa;IACb,+BAA+B;IAC/B,qBAAqB;IACrB,iBAAiB;IACjB,UAAU;AACd;;AAEA;;IAEI,kBAAkB;AACtB;;AAEA;;IAEI,kCAAkC;IAClC,mBAAmB;IACnB,oBAAoB;IACpB,mBAAmB