const { Ship } = require("./Ship.js")

class BoardTile
{
    #ship = null
    #hit = false
    #index = -1

    constructor(ship=null,hit=false,index=-1)
    {
        this.#ship = ship
        this.#hit=hit
        this.#index = index
    }

    updateWithShip(ship, index)
    {
        this.#ship = ship
        this.#index = index
    }

    isHit()
    {
        return this.#hit
    }

    hasShip()
    {
        return !(this.#ship == null)
    }


    hitTile()
    {
        this.#hit = true
        if (!this.hasShip())
        {
            return 0
        }

        this.#ship.hit(this.#index)

        if (!this.#ship.isSunk())
        {
            return 1
        }

        return 2
    }

    getShip()
    {
        return this.#ship
    }
}

class Gameboard
{
    #ships = []
    #board = []
    #size = 9

    #DEFAULT_BOARD_SPACE = {ship:null,hit:false,index:-1}

    constructor(size=9)
    {
        if (size < 2) { throw new Error("Invalid Size. Size cannot be less than 2") }
        for (let i = 0; i < size; i++)
        {
            let temp = []
            for (let j = 0; j < size; j++)
            {
                temp[j] = new BoardTile();
            }
            this.#board.push(temp)
        }
        this.#size = size
    }
    

    isValidLocation(location, length, isVertical)
    {
        if (location.length != 2 || location[0] < 0 || location[1] < 0)
        {
            return false
        }
        let x = location[0]
        let y = location[1]
        if ((isVertical && y+length >= this.#size) || (!isVertical && x+length >= this.#size))
        {
            return false
        }

        //Check if ship already exists in any spots
        for (let i = 0; i < length; i++)
        {
            if (isVertical && this.#board[location[0]][location[1]+i].hasShip())
            {
                return false
            }
            else if (!isVertical && this.#board[location[0]+i][location[1]].hasShip())
            {
                return false
            }
        }
        return true;
    }

    placeShip(shipType, location, isVertical=false)
    {
        let ship
        try
        {
            ship = new Ship(shipType)
        }
        catch (e) {
            return "Could not create ship with invalid ship type. " + e
        }
        if (!ship.isValidLocation(location, this.#size, isVertical))
        {
            return "Could not create ship at invalid location"
        }
        ship.setLocation(location, isVertical)
        for (let i = 0; i < ship.getLength(); i++)
        {
            if (isVertical)
            {
                this.#board[location[0]][location[1]+i].updateWithShip(ship, i)
            }
            else
            {
                this.#board[location[0]+i][location[1]].updateWithShip(ship, i)
            }
        }

        this.#ships.push(ship)

        return ""
    }

    isValidAttack(location)
    {
        if (location.length != 2 || location[0] < 0 || location[1] < 0 || location[0] >= this.#size || location[1] >= this.#size)
        {
            return false
        }

        if (this.#board[location[0]][location[1]].isHit())
        {
            return false
        }

        return true
    }

    receiveAttack(location)
    {
        /*
        Function that takes a board location and checks if it was a miss, if it was a hit or if it was a hit that sunk a ship.
        It returns a number with 0 being miss, 1 being hit and 2 being a sinking hit.
        */
        if (!this.isValidAttack(location))
        {
            return false
        }
        return this.#board[location[0]][location[1]].hitTile()

    }

    hasBeenAttacked(location)
    {
        let tile = this.#board[location[0]][location[1]]
        
        return tile.isHit()
    }

    allShipsSunk()
    {
        for (let i = 0; i < this.#ships.length; i++)
        {
            if (!this.#ships[i].isSunk())
            {
                return false
            }
        }

        return true
    }

    getPlayerBoard()
    {
        /*
        Returns a 2D array of integers representing the board state from the player perspective
        -1 = Empty, 0 = Miss, 1=Hit but not sunk, 2=Hit and Sunk, 3=Ship
        */
       let returnArray = []
       for (let i = 0; i < this.#size; i++)
       {
            let row = []
            for (let j = 0; j < this.#size; j++)
            {
                let tile = this.#board[i][j]
                if (!tile.isHit())
                {
                    if (tile.hasShip())
                    {
                        row.push(3)
                    }
                    else
                    {
                        row.push(-1)
                    }
                }
                else if (!tile.hasShip())
                {
                    row.push(0)
                }
                else if (!tile.getShip().isSunk())
                {
                    row.push(1)
                }
                else
                {
                    row.push(2)
                }
            }
            returnArray.push(row)
       }

       return returnArray
    }

    getShipStatus()
    {
        let result = []
        for (let i = 0; i < this.#ships.length; i++)
        {
            result.push(this.#ships[i].getHitSections())
        }

        return result
    }

    getEnemyBoard()
    {
        /*
        Returns a 2D array of integers representing the board state from an enemy perspective
        */
       let returnArray = []
       for (let i = 0; i < this.#size; i++)
       {
            let row = []
            for (let j = 0; j < this.#size; j++)
            {
                let tile = this.#board[i][j]
                if (!tile.isHit())
                {
                    row.push(-1)
                }
                else if (!tile.hasShip())
                {
                    row.push(0)
                }
                else if (!tile.getShip().isSunk())
                {
                    row.push(1)
                }
                else
                {
                    row.push(2)
                }
            }
            returnArray.push(row)
       }

       return returnArray
    }

    reset()
    {
        this.#board = []
        this.#ships = []
        for (let i = 0; i < this.#size; i++)
        {
            let temp = []
            for (let j = 0; j < this.#size; j++)
            {
                temp[j] = new BoardTile();
            }
            this.#board.push(temp)
        }
    }

    getSize()
    {
        return this.#size
    }

    getShips()
    {
        return this.#ships
    }

    getBoard()
    {
        return this.#board
    }
};

module.exports =  { Gameboard };